* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1, h1, h3, h4, h5, h6, p {
  font-size: inherit;
}

input, button {
  border: none;
  font-family: inherit;
  font-size: inherit;
}

input:focus, button:focus {
  outline: none;
}

ul, li {
  list-style: none;
}

button, input[type=submit] {
  background: none;
  cursor: pointer;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  color: #323232;
}

a {
  color: inherit;
  text-decoration: none;
}

.conteiner {
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}
.conteiner--fluid {
  max-width: unset;
  width: 100%;
}
.conteiner--no-pading {
  padding: 0;
}

.visual-hiden {
  opacity: 0;
  visibility: hidden;
  width: 1px;
  height: 1px;
  display: block;
  margin-left: -1000px;
  background-color: aliceblue;
  font-size: 0;
}

.none {
  display: none;
}

.nav {
  padding-top: 35px;
  padding-bottom: 33px;
}

.nav__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
  font-size: 24px;
  font-weight: 700;
  line-height: 1;
}
.logo img {
  margin-right: 5px;
}

.navigation-list ul {
  display: flex;
}
.navigation-list li {
  margin-right: 42px;
}
.navigation-list li:last-child {
  margin-right: 0px;
}
.navigation-list a:hover {
  color: #F78434;
}
.navigation-list a.ative {
  color: #F78434;
  font-weight: 700;
}

.header__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__text {
  width: 465px;
}
.header__text p {
  line-height: 1.6875;
  margin-bottom: 1em;
}

.header__title {
  margin-right: -170px;
  margin-bottom: 25px;
  font-size: 54px;
  font-weight: 700;
  line-height: 1.46;
  position: relative;
  z-index: 9;
}

.header__img img {
  border-radius: 40px 40px 40px 0;
}

.header__form {
  margin-top: 42px;
}

.form {
  background: #FFFFFF;
  box-shadow: 0px 17px 42px rgba(0, 0, 0, 0.12);
  border-radius: 29px;
  height: 58px;
  display: flex;
  transition: box-shadow 0.2s ease-in;
}
.form--active {
  box-shadow: 0px 17px 42px rgba(247, 132, 52, 0.27);
}

.form__label {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 72px;
  height: 100%;
}
.form__label::before {
  position: absolute;
  top: 20px;
  left: 19px;
  content: "";
  display: block;
  width: 24px;
  height: 19px;
  background-image: url("./../img/icons/mail.svg");
}

.form-input {
  flex-grow: 1;
  margin-right: 10px;
}
.form-input::placeholder {
  color: #bbb;
}

.button {
  display: inline-block;
  height: 58px;
  padding-left: 37px;
  padding-right: 37px;
  border-radius: 29px;
  background-color: #F78434;
  line-height: 58px;
  font-weight: 700;
  color: #fff;
  transition: background-color 0.2s ease-in;
}
.button:hover {
  background-color: #ff6a00;
}
.button--white {
  background-color: white;
  color: #323232;
  transition: color 0.2s ease-in, background-color 0.2s ease-in;
}
.button--white:hover {
  color: #fff;
  background-color: #323232;
}
.button--play {
  padding-left: 50px;
  position: relative;
}
.button--play::before {
  position: absolute;
  left: 30px;
  top: 50%;
  transform: translateY(-50%);
  content: "";
  display: block;
  width: 12px;
  height: 12px;
  background-image: url(/img/icons/play.svg);
  background-size: initial;
  background-repeat: no-repeat;
}

.clients {
  padding-top: 100px;
  padding-bottom: 90px;
}

.clients__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.objects {
  padding-top: 80px;
  padding-bottom: 80px;
}

.objects__row {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
}
@media (max-width: 1495px) {
  .objects__row {
    justify-content: flex-end;
  }
}

.objects__img {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

.objects__text {
  width: 374px;
  padding-left: 181px;
  flex-shrink: 0;
  padding-right: 150px;
  box-sizing: content-box;
}
.objects__text p + p {
  margin-top: 20px;
}

.objects__title {
  margin-bottom: 20px;
}

.objects__button {
  margin-top: 40px;
}

.title {
  font-size: 42px;
  line-height: 1.476;
  font-weight: 700;
}

.img-card {
  position: relative;
}
.img-card + .img-card {
  margin-left: 32px;
}

.img-car__img {
  border-radius: 20px 20px 0 20px;
}
.img-card--rounder .img-car__img {
  border-radius: 20px;
}

.img-card__info {
  position: absolute;
  right: -46px;
  bottom: 87px;
}

.card-info {
  display: flex;
  align-items: center;
  padding: 30px;
  background-color: #ffffff;
  box-shadow: 4px 20px 42px -2px rgba(0, 0, 0, 0.12);
  border-radius: 15px;
}

.card-info__icon {
  margin-right: 20px;
}

.card-info__title {
  font-weight: 700;
}

.cta {
  padding-top: 70px;
  padding-bottom: 70px;
}

.cta__block {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  border-radius: 20px;
  color: white;
  background-color: #58BEBF;
}

.cta__img {
  display: flex;
  margin-top: 60px;
  padding-top: 25px;
  padding-right: 25px;
  background-color: white;
  border-radius: 0 20px 0 0;
}

.cta__text {
  width: 555px;
  padding-left: 95px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: url(./../img/cta/Oval-boton-left.svg) bottom left -186px no-repeat, url(./../img/cta/Oval-top-rigth.svg) right top no-repeat;
}
.cta__text p {
  min-width: 320px;
}

.cta__title {
  margin-bottom: 15px;
}

.cta__button-row {
  margin-top: 30px;
  display: flex;
  column-gap: 20px;
}

.serve {
  position: relative;
  padding-top: 0px;
  padding-bottom: 120px;
}
.serve::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: #F5F6FA;
  position: absolute;
  bottom: 50%;
  top: -50%;
  left: 0;
  z-index: -1;
}

.serve__heading {
  margin-bottom: 50px;
  text-align: center;
}
.serve__heading .title {
  margin-bottom: 10px;
}

.serve__row {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  column-gap: 20px;
}

.card-customer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 88px;
  background-color: #FFF;
  box-shadow: 0px 40px 52px -22px rgba(0, 0, 0, 0.08);
  border-radius: 10px;
  text-align: center;
  transition: background-color 0.2s ease-in, color 0.2s ease-in;
}
.card-customer__icon {
  fill: #F78434;
  transition: fill 0.2s ease-in;
  margin-bottom: 15px;
}
.card-customer:hover {
  background-color: #F78434;
  color: #FFF;
}
.card-customer:hover .button {
  background-color: #FFF;
  color: #F78434;
}
.card-customer:hover .card-customer__icon {
  fill: #fff;
}

.card-customer__text {
  margin-bottom: 25px;
}

.benefits {
  padding-bottom: 110px;
}

.benefits__heading {
  padding-bottom: 60px;
  text-align: center;
}

.benefits__row {
  display: flex;
  justify-content: space-between;
  column-gap: 35px;
}

.benefits-item {
  text-align: center;
}
.benefits-item__icon {
  margin-bottom: 15px;
  display: inline;
}
.benefits-item__icon circle {
  fill: rgba(88, 190, 191, 0.2);
  transition: fill 0.2s ease-in;
}
.benefits-item__icon path, .benefits-item__icon ellipse {
  fill: #58BEBF;
  transition: fill 0.2s ease-in;
}
.benefits-item:hover .benefits-item__icon {
  margin-bottom: 15px;
  display: inline;
}
.benefits-item:hover .benefits-item__icon circle {
  fill: #F78434;
  transition: fill 0.2s ease-in;
}
.benefits-item:hover .benefits-item__icon path, .benefits-item:hover .benefits-item__icon ellipse {
  fill: #FFF;
  transition: fill 0.2s ease-in;
}

.benefit-item__title {
  margin-bottom: 2px;
  font-size: 28px;
}

.testimonials {
  padding-top: 60px;
  padding-bottom: 60px;
}

.testimonials__heading {
  margin-bottom: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.testimonials__title {
  max-width: 452px;
}

.testimonials__subtitle {
  max-width: 400px;
}

.testimonials__row {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 30px;
}
.testimonials__row::after {
  content: "";
  position: absolute;
  right: -40px;
  top: -40px;
  width: 80px;
  height: 80px;
  background-image: url(./../img/testimoniols/Shape.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.card-testimonial {
  display: inline-block;
  border-radius: 5px;
  overflow: hidden;
  max-width: 350px;
  transition: box-shadow 0.2s ease-in;
}
.card-testimonial:hover {
  box-shadow: 0px 10px 52px rgba(0, 0, 0, 0.08);
}
.card-testimonial:hover .card-testimonials__city {
  color: #F78434;
}

.card-testimonial__img-warper {
  position: relative;
}

.card-testimonial__btn-play {
  position: absolute;
  right: 20px;
  bottom: 20px;
  transition: translateY 0.1s ease-in, opacity 0.1s ease-in;
  transform: translateY(25%);
  opacity: 0;
}
.card-testimonial:hover .card-testimonial__btn-play {
  transform: translateY(0%);
  opacity: 1;
}

.card-testimonial__btn-svg circle {
  fill: #fff;
  transition: fill 0.2s ease-in;
}
.card-testimonial__btn-svg path {
  fill: #F78434;
  transition: fill 0.2s ease-in;
}
.card-testimonial__btn-svg:hover circle {
  fill: #F78434;
  transition: fill 0.2s ease-in;
}
.card-testimonial__btn-svg:hover path {
  fill: #fff;
  transition: fill 0.2s ease-in;
}

.card-testimonials__body {
  padding: 30px;
}

.card-testimonials__text {
  margin-bottom: 23px;
}

.card-testimonials__name {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 1px;
}

.card-testimonials__city {
  color: #58BEBF;
  transition: color 0.2s ease-in;
}

.contacts {
  position: relative;
  padding-top: 200px;
  padding-bottom: 55px;
  background-image: url(./../img/contact/footer-bg.svg);
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
}
.contacts::before {
  position: absolute;
  bottom: -1px;
  left: 50%;
  content: "";
  display: block;
  width: 743px;
  height: 372px;
  background-image: url(./../img/contact/foter-oval.svg);
  background-size: initial;
}
.contacts::after {
  position: absolute;
  bottom: 0;
  left: 50%;
  content: "";
  display: block;
  width: 478px;
  height: 547px;
  margin-left: 79px;
  background-image: url(./../img/contact/giril.png);
  background-size: initial;
}

.contact__info {
  max-width: 360px;
}

.contact__text {
  max-width: 342px;
  margin-bottom: 30px;
}

.contact__title {
  margin-bottom: 15px;
}

.footer {
  padding-top: 86px;
  padding-bottom: 82px;
}

.footer__info {
  max-width: 300px;
  line-height: 27px;
  font-style: normal;
  font-weight: 400;
}

.footer__logo {
  margin-bottom: 15px;
  margin-left: -7px;
}

.footer__info-text {
  margin-bottom: 40px;
}

.footer__social {
  display: flex;
  column-gap: 30px;
  align-items: center;
}
.footer__social a:hover {
  transition: opacity 0.2s ease-in;
  opacity: 0.5;
}

.footer__row {
  display: flex;
  justify-content: space-between;
}

.footer__nav {
  display: flex;
  justify-content: space-between;
  column-gap: 85px;
}

.footer__nav-block {
  font-size: 14px;
}
.footer__nav-block li {
  margin-bottom: 13px;
}
.footer__nav-block a:hover {
  transition: color 0.2s ease-in;
  color: #F78434;
}

.footer__nav-block-title {
  margin-bottom: 18px;
  font-size: 18px;
  font-weight: 700;
}